<template>
  <div class="detail" id="top">
    <contents />
  </div>
</template>

<script>
import Vue from "vue";
import VueHead from "vue-head";
import game from "@/data/game.json";

import contents from "@/components/detail_contents.vue";

Vue.use(VueHead);

export default {
  name: "index",
  components: {
    contents
  },
  created() {
    this.gameId = this.$route.params.id;
    this.game = game[this.gameId];
  },
  head: {
    title() {
      return {
        inner: `${this.game.title}`,
        separator: "",
        complement: ""
      };
    },
    meta() {
      return [
        {
          name: "title",
          content: `${this.game.title}`
        },
        {
          name: "description",
          content: `${this.game.title}`
        },
        {
          name: "keywords",
          content: `${this.game.title},,,`
        },
        {
          property: "og:title",
          content: `${this.game.title}`
        },
        { property: "og:type", content: "website" },
        {
          property: "og:description",
          content: `${this.game.title}`
        },
        {
          property: "og:site_name",
          content: ""
        },
        {
          property: "og:image",
          content: `https://debugmonkeys.netlify.app/ogp_${this.gameId}.jpg`
        },
        { name: "twitter:site", content: "@debug_monkeys" },
        { name: "twitter:card", content: "summary_large_image" }
      ];
    }
  }
};
</script>
